body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background: -webkit-linear-gradient(1deg, #a7237f 0%, rgb(45, 118, 126) 100%);
  background: linear-gradient(89deg, #a7237f 0%, rgb(45, 118, 126) 100%); /* w3c */
}

button {
  cursor: pointer;
}

.centre{
  justify-content: center;
}


.Tilt {
  background: -webkit-linear-gradient(1deg, #FF5EDF 0%, #04C8DE 100%);
  background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%); /* w3c */
}

.logo{
  padding-top: 10px;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

